  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-family: "icomoon";
  content: "\e918";
  color: #bbb;
  font-size: 8px;
}
.footer-nav__col:last-of-type {
  margin-right: 0px;
}
@media screen and (max-width: 480px) {
  .footer-nav__col:last-of-type {
    margin-right: 0;
  }
}
.footer-nav__col:last-of-type ul li {
  display: flex;
  color: #BBBBBB;
  line-height: 170%;
  margin-bottom: 19px;
}
@media screen and (max-width: 480px) {
  .footer-nav__col:last-of-type ul li {
    justify-content: center;
  }
}
.footer-nav__col:last-of-type ul li i {
  margin-right: 15px;
  padding-top: 4px;
  font-size: 21px;
}
.footer-nav__col:last-of-type ul li .footer-nav__col-phones a {
  display: block;
  color: #FFFFFF;
  font-size: 16px;
  margin-bottom: 0;
  line-height: normal;
  padding-left: 0;
}
.footer-nav__col:last-of-type ul li .footer-nav__col-phones a:before {
  display: none;
}
.footer-nav__col:last-of-type ul li .footer-nav__col-phones a:hover {
  opacity: 0.8;
}
.footer-copy {
  padding: 16px 0 20px;
  font-size: 14px;
  line-height: 170%;
  color: #999999;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.footer-copy p {
  margin: 0;
  font-family: "Pistoletto-Black", sans-serif;
  font-size: 20px;
}
@media screen and (max-width: 480px) {
  .footer-copy p {
    font-size: 16px;
  }
}
.footer-copy p a {
  color: #d05278;
  text-decoration: none;
  transition: all 0.3s ease;
}
.footer-copy p a:hover {
  color: #fff;
}
.footer-copy p span {
  color: #d05278;
}

