@media screen and (max-width: 768px) {
  .footer-top {
    flex-wrap: wrap;
    text-align: center;
  }
  .footer-top__logo {
    order: 2;
    margin-top: 0;
  }
  .footer-top__social {
    order: 1;
    margin: 10px 0;
  }
  .footer-top__social ul {
    justify-content: center;
  }
  .footer-top__payments {
    order: 3;
    max-width: 33.33333%;
  }
  .footer-top__payments ul {
    margin: 0;
    justify-content: center;
  }
  .footer-top__payments ul li {
    width: auto;
    margin-bottom: 0;
  }
  .footer-top__payments ul li:nth-child(1) {
    margin-bottom: 12px;
  }
  .footer-nav {
    padding: 24px 0 24px;
    flex-wrap: nowrap;
    text-align: center;
    margin: 0 auto;
  }
  .footer-nav__col {
    max-width: 100%;
    width: 45%;
    margin-bottom: 25px;
  }
  .footer-nav__col:last-of-type {
    margin-bottom: 0;
  }
}
@media screen and (max-width: 480px) {
  .footer-top {
    padding: 10px 0 25px;
    text-align: center;
  }
  .footer-top__logo {
    order: 1;
    margin-top: 0;
  }
  .footer-top__social {
    order: 2;
    margin: 10px 0;
  }
  .footer-top__social ul {
    justify-content: center;
  }
  .footer-top__payments {
    order: 3;
    max-width: 100%;
  }
  .footer-top__payments ul {
    margin: 0;
    justify-content: center;
  }
  .footer-top__payments ul li {
    width: auto;
    margin-bottom: 0;
  }
  .footer-top__payments ul li:nth-child(2) {
    margin-bottom: 12px;
  }
  .footer-nav {
    flex-wrap: wrap;
  }
  .footer-nav__col {
    width: 100%;
  }
  .footer-nav__col-title {
    text-align: center;
    width: 100%;
  }
}